"use client";

import { motion } from "motion/react";
import { useSidebar } from "./sidebar-provider";

export function ContentWrapper({ children }: { children: React.ReactNode }) {
  const { width } = useSidebar();

  return (
    <motion.div
      animate={{
        marginLeft: width,
      }}
      transition={{
        duration: 0.3,
        ease: [0.22, 1, 0.36, 1],
      }}
      className="min-h-screen"
    >
      {children}
    </motion.div>
  );
}

